<script>
export default {
    name: 'Sharecheck',

    methods: {
        showShareCheck() {
            this.$emit("showShareCheck", false)
        }
    }
}
</script>

<style lang="scss">
    .shareCheck {
        position: fixed;
        bottom: 0;

        width: 100%;

        font-family: SFPro;
        font-weight: 400;
        font-size: 17px;
        line-height: 22px;
        letter-spacing: -0.408px;
        color: #000;

        border-radius: 13px 13px 0px 0px;
        background: rgb(250, 250, 250);

        z-index: 3;

        &__header {
            display: flex;
            justify-content: space-between;

            padding-bottom: 18px;

            border-bottom: 1px solid rgba(60, 60, 67, 0.36);

            &-logo {
                display: flex;
                align-items: center;

                & img {
                    margin-right: 8px;

                    // box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.05);
                }
            }

            &-title {
                font-weight: 600;
                font-size: 15px;
                line-height: 20px;
                letter-spacing: -0.24px;
            }

            &-link {
                font-size: 13px;
                line-height: 18px;
                letter-spacing: -0.078px;
                color: rgba(60, 60, 67, 0.6);
            }

            &-close {
                width: 30px;
                height: 30px;

                background: rgba(116, 116, 128, 0.08);

                text-align: center;

                border-radius: 50%;
                border: none;
            }
        }

        &__persons {
            padding: 20px 0;

            border-bottom: 1px solid rgba(60, 60, 67, 0.36);            
        }

        &__list {
            display: flex;
            justify-content: space-between;
            align-items: center;

            &-item {
                display: flex;
                flex-direction: column;
                align-items: center;

                font-size: 11px;
                line-height: 13px;
                letter-spacing: 0.066px;
                list-style-type: none;

                & img {
                    margin-bottom: 4px;
                }

                & .gray-text {
                    color: rgba(60, 60, 67, 0.6);
                }
            }
        }

        & .social {
            padding: 20px 0;
            margin-bottom: 4px;
        }

        & .action__list {
            margin-bottom: 16px;

            background: #FFFFFF;
            border-radius: 13px;

            &-item {
                display: flex;
                justify-content: space-between;
                padding: 13px 16px 15px;

                border-bottom: 1px solid rgba(60, 60, 67, 0.36);

                &:last-child {
                    border-bottom: none;
                }
            }
        }

        &__link {
            margin-left: 16px;

            color: #007AFF;
            text-decoration: none;
        }
    }
</style>