<template>
    <div class="container">
        <div class="myOrder">
            <h1 class="myOrder__heading">Мой заказ</h1>
            
            <span class="myOrder__table">Стол 8</span>
        </div>

        <div class="dishList">
            <div class="emptyList" v-if="!dishItem">
                <img src="@/assets/images/clock_logo.svg" alt="Изображение часов" class="emptyList__icon">
                
                <h3 class="emptyList__heading">Скоро здесь появится ваш заказ</h3>
                
                <p class="emptyList__explanation">Возможно, официант ещё не успел открыть заказ пожалуйста, подождите..</p>
            </div>

            <ul class="dishList__list" v-else>
                <li class="dishList__item" v-for="item in items" :key="item.id">
                    <p class="dishList__item-status">Не оплачено</p>

                    <div class="dishList__item-dish">
                        <label class="dish__checkbox">
                            <input type="checkbox" checked>

                            <span></span>

                            <p class="dish__checkbox-text">{{item.name}}</p>
                        </label>

                        <div class="dishList__item-total">
                            <span class="sum">{{item.price}} &#8381;</span>

                            <div class="count">
                                <button class="count__btn minus">
                                    <svg width="10" height="4" viewBox="0 0 10 4" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M0 3.25V0.75C0.910053 0.75 1.78307 0.75 2.61905 0.75C3.46561 0.75 4.25926 0.75 5 0.75C5.74074 0.75 6.53439 0.75 7.38095 0.75C8.22751 0.75 9.10053 0.75 10 0.75V3.25C9.10053 3.25 8.22751 3.25 7.38095 3.25C6.53439 3.25 5.74074 3.25 5 3.25C4.25926 3.25 3.46561 3.25 2.61905 3.25C1.78307 3.25 0.910053 3.25 0 3.25Z" fill="#808697"/>
                                    </svg>
                                </button>

                                <span class="count__quantity">1</span>

                                <button class="count__btn">
                                    <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M3.8208 10C3.8208 9.32728 3.8208 8.6781 3.8208 8.05247C3.8208 7.42011 3.8208 6.83148 3.8208 6.28658V3.72351C3.8208 3.17188 3.8208 2.58325 3.8208 1.95762C3.8208 1.32526 3.8208 0.672721 3.8208 0H6.1689C6.1689 0.672721 6.1689 1.32526 6.1689 1.95762C6.1689 2.58325 6.1689 3.17188 6.1689 3.72351V6.28658C6.1689 6.83148 6.1689 7.42011 6.1689 8.05247C6.1689 8.6781 6.1689 9.32728 6.1689 10H3.8208ZM0 6.12513V3.88496C0.686577 3.88496 1.34912 3.88496 1.98764 3.88496C2.63302 3.88496 3.23378 3.88496 3.78991 3.88496H6.19979C6.76279 3.88496 7.36354 3.88496 8.00206 3.88496C8.64744 3.88496 9.31342 3.88496 10 3.88496V6.12513C9.31342 6.12513 8.64744 6.12513 8.00206 6.12513C7.36354 6.12513 6.76279 6.12513 6.19979 6.12513H3.78991C3.23378 6.12513 2.63302 6.12513 1.98764 6.12513C1.34912 6.12513 0.686577 6.12513 0 6.12513Z" fill="#808697"/>
                                    </svg>
                                </button>
                            </div>
                        </div>
                    </div>
                </li> 

                <li class="dishList__item paid">
                    <p class="dishList__item-status">Оплачено</p>

                    <div class="dishList__item-dish">
                        <label class="dish__checkbox">
                            <input type="checkbox" checked disabled>

                            <span></span>

                            <p class="dish__checkbox-text">Да лука игристое розовое</p>
                        </label>

                        <div class="dishList__item-total">
                            <span class="sum">1200 &#8381;</span>

                            <div class="count">
                                <button class="count__btn minus">
                                    <svg width="10" height="4" viewBox="0 0 10 4" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M0 3.25V0.75C0.910053 0.75 1.78307 0.75 2.61905 0.75C3.46561 0.75 4.25926 0.75 5 0.75C5.74074 0.75 6.53439 0.75 7.38095 0.75C8.22751 0.75 9.10053 0.75 10 0.75V3.25C9.10053 3.25 8.22751 3.25 7.38095 3.25C6.53439 3.25 5.74074 3.25 5 3.25C4.25926 3.25 3.46561 3.25 2.61905 3.25C1.78307 3.25 0.910053 3.25 0 3.25Z" fill="#808697"/>
                                    </svg>
                                </button>

                                <span class="count__quantity">1</span>

                                <button class="count__btn">
                                    <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M3.8208 10C3.8208 9.32728 3.8208 8.6781 3.8208 8.05247C3.8208 7.42011 3.8208 6.83148 3.8208 6.28658V3.72351C3.8208 3.17188 3.8208 2.58325 3.8208 1.95762C3.8208 1.32526 3.8208 0.672721 3.8208 0H6.1689C6.1689 0.672721 6.1689 1.32526 6.1689 1.95762C6.1689 2.58325 6.1689 3.17188 6.1689 3.72351V6.28658C6.1689 6.83148 6.1689 7.42011 6.1689 8.05247C6.1689 8.6781 6.1689 9.32728 6.1689 10H3.8208ZM0 6.12513V3.88496C0.686577 3.88496 1.34912 3.88496 1.98764 3.88496C2.63302 3.88496 3.23378 3.88496 3.78991 3.88496H6.19979C6.76279 3.88496 7.36354 3.88496 8.00206 3.88496C8.64744 3.88496 9.31342 3.88496 10 3.88496V6.12513C9.31342 6.12513 8.64744 6.12513 8.00206 6.12513C7.36354 6.12513 6.76279 6.12513 6.19979 6.12513H3.78991C3.23378 6.12513 2.63302 6.12513 1.98764 6.12513C1.34912 6.12513 0.686577 6.12513 0 6.12513Z" fill="#808697"/>
                                    </svg>
                                </button>
                            </div>
                        </div>
                    </div>
                </li> 
            </ul>
        </div>

        <div class="tips">
            <span class="tips__heading">Чаевые</span>
                
            <div class="tips__waiter">
                <img src="@/assets/images/image_waiter.jpg" alt="Фото официанта">

                <div class="tips__waiter-info">
                    <span>Оффициант</span>

                    <span>Светлова Е.В</span>
                </div>
            </div>

            <div class="tips__sum">
                <span>0 &#8381;</span>
                    
                <button class="tips__sum-btn" type="reset">
                    <img src="@/assets/images/close_icon.svg" alt="Закрыть">
                </button>
            </div>

            <div class="tips__percents">
                <ul class="tips__percents-list">
                    <li class="tips__list-item" :class="{ 'active': percents == '0%' }" @click="selectPercents('0%')">0%</li>
                        
                    <li class="tips__list-item" :class="{ 'active': percents == '10%' }" @click="selectPercents('10%')">10%</li>
                        
                    <li class="tips__list-item" :class="{ 'active': percents == '15%' }" @click="selectPercents('15%')">15%</li>
                        
                    <li class="tips__list-item" :class="{ 'active': percents == '20%' }" @click="selectPercents('20%')">20%</li>
                        
                    <li class="tips__list-item" :class="{ 'active': percents == '25%' }" @click="selectPercents('25%')">25%</li>
                </ul>
            </div>
        </div>

        <div class="check">
            <div class="check__total">
                <span class="check__total-heading">Общий счёт:</span>
                
                <span class="check__total-sum">{{sum}} &#8381;</span>
            </div>

            <div class="check__position">
                <div class="check__tips">
                    <span class="check__tips-heading">Чаевые:</span>
                
                    <span class="check__tips-sum">0 &#8381;</span>
                </div>
                
                <div class="check__marked">
                    <span class="check__tips-heading">Отмеченные позиции:</span>
                
                    <span class="check__tips-sum">0 &#8381;</span>
                </div>
            </div>

            <div class="check__totalPay">
                <span class="check__totalPay-heading">Итого к оплате:</span>
                
                <span>0 &#8381;</span>
            </div>
        </div>
    
        <form action="" class="payment" @submit.stop.prevent="prevent">
            <button class="payment-btn main-btn" @click="showChoicePayment(true)">Оплатить</button>
        
            <button class="share-btn main-btn" @click="showShareCheck(true)">Поделиться счётом</button>
    
            <label class="payment__conditions"> 
                <input type="checkbox" checked class="payment__conditions-check">
                <p class="payment__conditions-text">Я хочу взять на себя транзакционные издержки (11 ₽), чтобы сотрудник получил полную сумму</p>
            </label>

            <label class="payment__conditions">
                <input type="checkbox" checked class="payment__conditions-check">
                
                <div class="payment__conditions-text">
                    <p>Согласен с условиями 
                        <a href="#">Пользовательского соглашения</a> и 
                    
                        <a href="#">Политикой обработки персональных данных</a>
                    </p>
                </div>
            </label>  
        </form>        
    </div>

    <div class="menu">
        
        <div class="callWaiter" @click="showCallWaiter(true)">
            <img src="~@/assets/images/waiter_icon.svg" alt="">
        </div>
        
    </div>

    <div>
        <choicepayment v-if="choicePaymentVisible" @showChoicePayment="showChoicePayment" @showPayment="showPayment"/>      
        <callwaiter v-if="callWaiter" @showCallWaiter="showCallWaiter"/>
        <payment v-if="paymentVisible" @showPayment="showPayment"/>    
        <sharecheck v-if="shareCheckVisible" @showShareCheck="showShareCheck"/>   
        <review v-if="reviewVisible" @showReview="showReview"/>
    </div>
</template>

<script>
import Choicepayment from "@/components/Choiсepayment"
import Callwaiter from "@/components/Callwaiter"
import Payment from "@/components/Payment"
import Sharecheck from "@/components/Sharecheck"
import Review from "@/components/Review"
    export default {
        name: 'Order',
        components: {
            Choicepayment,
            Callwaiter,
            Payment,
            Sharecheck,
            Review
        },

        data () {
            return {
                dishItem: false,
                choicePaymentVisible: false,
                callWaiter: false,
                paymentVisible: false,
                shareCheckVisible: false,
                reviewVisible: false,
                percents: "0%",
                items: [   
                    {id:0, name: "Тыквенный суп", price: 450},
                    {id:1, name: "Морские гребешки с соусом", price: 700},
                    {id:2, name: "Паста болоньезе с соусом песто", price: 375},
                    {id:3, name: "Чизкейк", price: 250}
                ]
            }
        },

        methods: {
            showChoicePayment (val) {
                this.choicePaymentVisible = val
               },

            showCallWaiter (val) {
                this.callWaiter = val
            },

            showPayment(val) {
                this.choicePaymentVisible = false
                this.paymentVisible = val
                setTimeout(() => {
                    this.paymentVisible = false 
                    this.reviewVisible = true
                }, 2000);
            },

            showShareCheck(val) {
                this.shareCheckVisible = val
            },

            selectPercents (val) {
                this.percents = val
            },

            showReview(val) {
                this.reviewVisible = val
            },

            LoadDishList() {
                setTimeout(() => {
                    this.dishItem = true
                }, 3000);
            },
        },

        mounted() {
            this.LoadDishList() 
        }
    }
</script>

<style lang="scss">
    .myOrder {
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;

        font-weight: 600;
        font-size: 20px;
        line-height: 25px;

        &__heading {
            font-weight: 600;
            font-size: 20px;
            line-height: 25px;
        }

        &__table {
           color: #80869A; 
        }
    }

    .dishList__list {
        & .paid {
            border: 1px solid #E6E7EB;
            color: #CCCFD7;

            & .dishList__item-status {
                color: #25C26E;
            }

            & .dish__checkbox-text {
                text-decoration: line-through;

                &:before {
                    border-color: #DCDEE3;
                }
            }

            & .sum {
                color: #CCCFD7;
                text-decoration: line-through;
            }

            & .count {
                opacity: 0.5;
            }
        }
    }

    .dishList__item {
        margin-top: 10px;
        padding: 12px;

        border: 1px solid #E6E7EB;
        border-radius: 12px;
        list-style-type: none;

        &.paid {
            & .dishList__item-dish {
                & .dish__checkbox {
                    & input:checked+span {
                        background-image: url('~@/assets/images/checked_paid.svg');
                    }
                }
            }
            
        }

     
        &-status {
            font-weight: 500;
            font-size: 13px;
            line-height: 18px;
            text-align: center;

            color: #80869A;
        }

        &-dish {
            display: flex;
            justify-content: space-between;

            & .dish__checkbox {
                display: flex;
                align-items: center;

                & span {
                    content: '';
                    display: inline-block;
                    width: 14px;
                    height: 14px;
                    flex-shrink: 0;
                    flex-grow: 0;
                    border: 1.5px solid #DCDEE3;
                    border-radius: 0.25em;
                    margin-right: 21px;
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: 12px;
                }

                & input {
                    position: absolute;
                    z-index: -1;
                    opacity: 0;

                    &:checked+span {
                        background-image: url('~@/assets/images/checked.65527f52.svg');
                    }
                }

                &-text {
                    display: inline-flex;
                    align-items: center;
                    max-width: 170px;

                    font-size: 16px;
                    line-height: 20px;
                }
            }
        }

        &-total {
            display: flex;
            flex-direction: column;

            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            text-align: right;

            color: #413DFF;


            & .count {
                display: flex;
                align-items: center;
                margin-top: 8px;
                padding: 3px 7px;

                border: 1px solid #EDEEF1;
                border-radius: 6px;
                background: #F7F8FA;

                font-weight: normal;
                font-size: 13px;
                line-height: 16px;

                &__quantity {
                    font-size: 13px;
                    line-height: 18px;

                    color: #80869A;
                }

                &__btn {
                    padding: 0;

                    outline: none;
                    border: none;
                    background-color: inherit;
                    cursor: pointer;

                    & svg {
                        vertical-align: middle;
                    }
                }

                & span {
                    margin: 0 11px;
                }
            }
        }
    }  
    
    .emptyList {
        margin-bottom: 31px;
        padding: 17px 0 ;

        text-align: center;

        &__icon {
            margin-bottom: 18px;
        }

        &__heading {
            margin: 0 auto;
            margin-bottom: 16px;
            max-width: 298px;
            
            font-weight: 600;
            font-size: 24px;
            line-height: 30px;
        }

        &__explanation {
            margin: 0 auto;
            max-width: 279px;

            font-weight: 500;
            font-size: 16px;
            line-height: 20px;

            color: #343D5D;
        }        
    }

    

    .tips {
        margin-top: 24px;
        margin-bottom: 24px;
        padding: 11px;

        border: 1px solid #E6E7EB;
        border-radius: 11px;

        &__heading {
            font-weight: 600;
            font-size: 17px;
            line-height: 22px;
        }

        &__waiter {
            margin-top: 16px;
            margin-bottom: 13px;

            & img {
                margin-right: 12px;
            }

            &-info {
                display: inline-flex;
                flex-direction: column;

                vertical-align: top;
                
                font-weight: 400;
                font-size: 13px;
                line-height: 18px;
                color: #343D5D;

                & span:first-child {
                    color: #80869A;
                }
            }
        }

        &__sum {
            position: relative;

            padding-bottom: 19px;

            font-weight: 600;
            font-size: 17px;
            line-height: 22px;

            text-align: center;

            border-bottom: 1px solid #EDEEF1;

            &-btn {
                position: absolute;
                right: 0;
                top: 0;

                width: 24px;
                height: 24px;
                display: flex;
                justify-content: center;
                align-items: center;

                outline: none;
                background: #F7F8FA;
                cursor: pointer;

                border-radius: 50%;
                border: 1px solid #EDEEF1;
            }
        }

        &__percents {
            padding-top: 18px;

            &-list {
                display: flex;
                justify-content: space-between;
                list-style-type: none;

                & .tips__list-item {
                    padding: 4px;

                    font-weight: 500;
                    font-size: 16px;
                    line-height: 20px;
                    color: #80869A;
                    cursor: pointer;

                    background: #F7F8FA;
                    border-radius: 6px;
                    border: 1px solid #F7F8FA;

                    &:hover {
                        color: #010D35;
                        border: 1px solid #6764FF;
                    }
                }

                & .active {
                    color: #010D35;
                    border: 1px solid #6764FF;
                }
            }
        }
    }

    .check {
        margin-bottom: 24px;
        padding: 12px;

        border: 1px solid #DBDEE7;
        border-radius: 11px;

        &__total {
            display: flex;
            justify-content: space-between;
            padding-bottom: 4px;

            font-weight: 600;
            font-size: 17px;
            line-height: 22px;

            border-bottom: 1px solid #EDEEF1;
        }

        &__position {
            padding-bottom: 16px;
            padding-top: 8px;

            font-weight: 400;
            font-size: 15px;
            line-height: 20px;

            border-bottom: 1px solid #EDEEF1;

            & .check__tips {
                display: flex;
                justify-content: space-between;
                margin-bottom: 6px;

                color: #343D5D;
            } 

            & .check__marked {
                display: flex;
                justify-content: space-between;
            }
        }        

        &__totalPay {
            display: flex;
            justify-content: space-between;
            padding-top: 15px;

            font-weight: 600;
            font-size: 20px;
            line-height: 25px;
        }
    }

    .payment {
        margin-bottom: 49px;

        & .main-btn {
            width: 100%;
            padding-top: 16px;
            padding-bottom: 16px;
            margin-bottom: 12px;

            font-family: Commissioner;
            text-align: center;
            font-weight: 600;
            font-size: 17px;
            line-height: 22px;

            border-radius: 12px;
            border: none;

            outline: none;
            cursor: pointer;
        }

        & .payment-btn {
            color: #fff;
            letter-spacing: 0.03em;
            background: linear-gradient(223.61deg, #547AFF 0%, #413DFF 100%);
        }

        & .share-btn {
            color: #343D5D;
            background: #EDEEF1;
        }

        &__conditions {
            display: flex;
            align-items: center;
            margin-bottom: 8px;

            font-weight: 400;
            font-size: 12px;
            line-height: 16px;

            color: #80869A;

            &-check {
                position: absolute;
                z-index: -1;
                opacity: 0;

                &:checked+.payment__conditions-text::before {
                    background-image: url('~@/assets/images/paid_check.d789c5ba.svg');
                }
            } 

            &-text {
                display: inline-flex;
                align-items: center;
                user-select: none;

                &:before {
                    content: '';
                    display: inline-block;
                    width: 14px;
                    height: 14px;
                    flex-shrink: 0;
                    flex-grow: 0;
                    border: 1.5px solid #DCDEE3;
                    border-radius: 0.4em;
                    margin-right: 17px;
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: 12px;
                }

                & a {
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 16px;

                    color: #80869A;
                }
            }
        }
    }

    .menu {
        position: fixed;
        bottom: 0;

        width: 100%;
        padding-bottom: 62px;

        border-top: 1px solid #CCCFD7;
        background-color: #fff;

        z-index: 3;

        & .callWaiter {
            position: absolute;
            left: 50%;
            top: -50%;

            display: flex;
            justify-content: center;
            align-items: center;
            padding: 5px;

            border-radius: 100px;
            border-top: 2px solid #fff;
            border-left: 2px solid #fff;
            border-bottom: 2px solid #CCCFD7;
            border-right: 2px solid #CCCFD7;
            background-color: #fff;
            transform: translateX(-50%) rotate(45deg);

            & img {
                transform: rotate(-45deg);
            }

        }

        
    }
</style>