<template>
  <div> 
    <loadscreen v-if="!screenSaver"/>
    <order v-else/>
  </div>
</template>

<script>
import Loadscreen from "@/components/Loadscreen"
import Order from "@/components/Order"
  export default {
    name: 'App',
    components: {
      Order,
      Loadscreen
    },

    data () {
      return {
        screenSaver: false
      }
    },

    methods: {
      LoadScreen() {
        setTimeout(() => {
          this.screenSaver = true
        }, 3000);
      }
    },

    mounted() {
      this.LoadScreen()
    }
  }
</script>

